:root {
    /* --dark-gray-color: rgb(22, 21, 22); */
    --dark-gray-color: #FFF;
    --blue-color: rgba(52, 152, 219, 0.40);
    --purple-color: rgba(155, 89, 182, 0.60);
    --purple-dark-color: rgb(85, 46, 99);
    --gray-color: #86939d;
    --gray-dark-color: rgb(39, 44, 48);
    --red-color: #d63031;
}
header {
    width: 100%;
    min-height: 70px;
    max-height: 80px;
    margin: 0 auto;
    margin-bottom: 50px;
    display: -webkit-flex;
    display: flex;
    padding-top: 5px;
    padding-bottom: 5px;
    -webkit-justify-content: center;
            justify-content: center;
    place-items: center;
    /* border: 1px solid red; */
    /* border-bottom: 1px solid rgb(211, 211, 211); */
    box-shadow: 0px 0.5px 5px 1px rgba(0, 0, 0, 0.25);
}

.container-header {
    width: 80%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    place-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    /* border: 1px solid red; */
}

nav:nth-of-type(1) {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

nav:nth-of-type(2) {
    max-width: 50%;
    min-width: 25%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    /* border: 1px solid red; */
}

nav a {
    color: rgb(39, 39, 39);
    text-decoration: none;
    font-weight: 400;
    transition-property: color;
    transition-duration: .5s;
}

nav a:hover {
    color: var(--purple-dark-color);
    /* font-weight: 600; */
}

nav:nth-of-type(1) img,
svg {
    width: 80%;
    height: 100%;
    /* border:1px solid red; */
    margin: auto 0;
}


