footer {
    width: 100%;
    min-height: 90px;
    /* max-height: 110px; */
    margin-top: 25px;
    display: -webkit-flex;
    display: flex;
    place-items: center;
    box-shadow: 0px 0.5px 5px 1px rgba(0, 0, 0, 0.25);
}

.footer__container {
    width: 90%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    place-items: center;
}

.footer__nav strong{
    background-color: var(--purple-dark-color);
    color: #fff;
    padding: 2px 6px;
    border-radius: 3px;
}
.footer__nav:nth-last-of-type(1){
    min-width: 10%;
    /* border: 2px solid red; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.footer__container .footer__nav .icon img {
    width: 30px;
}
.container-layout {
    /* position: relative; */
    width: 100%;
    height: auto;
    /* background:
        radial-gradient(farthest-corner circle at 10% 20%, var(--blue-color) 0%, transparent 38%) top left,
        radial-gradient(farthest-corner circle at 90% 80%, var(--purple-color) 0%, transparent 38%)  bottom right;
    background-position: center;
    background-repeat: no-repeat;
    backdrop-filter: blue(10px); */
}

.container-title {
    width: 50%;
    margin: 0 auto;
    margin-bottom: 35px;
    /* border: 1px solid blue; */
}

.title {
    color: var(--gray-dark-color);
    font-size: 2em;
    line-height: 1.8;
}

.title::before {
    background-color: var(--purple-dark-color);
    width: 45px;
    height: 3px;
    display: block;
    content: "";
}

.description {
    color: var(--gray-color);
    font-size: .88889em;
    font-weight: 400;
}
.card {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px;
    /* grid-template-rows: 50% 20 30%; */
}


.card__thumbnail {
    width: 100%;
    height: 170px;

    border-radius: 15px 15px 0px 0px;
    overflow: hidden;
}

.card__thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card__title {
    text-transform: uppercase;
    margin-top: 20px;
    margin-bottom: 8px;
    font-size: 1.7em;
    line-height: 1.5;
}

.card__title a{
    color: var(--gray-dark-color);
    text-decoration: none;
}
.container-videos {
    width: 90%;
    /* border: 1px solid red; */
    margin: 0 auto;
    /* margin-top: 20px; */
}

.container-cards-video{
    width: 90%;
    margin: auto;
    display: grid; 
    grid-auto-flow: row dense; 
    grid-template-columns: repeat(3, 1fr); 
    /* grid-template-rows: 1fr 1fr 1fr;  */
    grid-gap: 10px 10px;
    gap: 10px 10px; 
}


.container-contact {
    width: 90%;
    /* border: 1px solid red; */
    margin: 0 auto;
    /* margin-top: 20px; */
}
.container-networks {
    width: 100%;
    min-height: 140px;
    /* border: 1px solid blue; */
    margin-bottom: 30px;
    
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    place-items: center;
}

.item__card a {
    color: var(--gray-dark-color);
    text-decoration: none;
}

.item__card h2 {
    font-size: 1.5rem;
    font-weight: 700;
    letter-spacing: -.03em;
    padding: 0 2em 0 0;
    line-height: 1;
    text-transform: uppercase;
    text-align: left;
}

.item__card p {
    margin-top: 18px;
    letter-spacing: 2px;
}

.item__card h2 span {
    display: block;
    text-align: left;
    font-size: .85rem;
    font-weight: 500;
}


.network__card {
    display: -webkit-flex;
    display: flex;
}

.network__card figure img {
    width: 48px;
    height: auto;
    margin: 0 1em 0 0;
    padding: 0;
}

.network__card-title {
    font-size: 1.1rem;
    text-transform: uppercase;
}

.network__card-title a h2 span {
    display: block;
    letter-spacing: 3.2px;
    text-align: left;
    font-size: .85rem;
    font-weight: 500;
}

.network__card-title p {
    letter-spacing: 3.2px;
}
.network__card-title a {
    text-decoration: none;
    color: #272c30;
}
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body {
    background-color: var(--dark-gray-color);
    /* font-family: 'Roboto', sans-serif; */
    font-family: 'Karla', sans-serif;
    /* overflow: hidden; */
    
}

h1, h2, h3, h4, h5 {
    font-weight: 700;
    line-height: 1.1;
    /* margin: 2.5rem 0 .83333rem; */
    text-rendering: optimizeLegibility;
}

.container-home {
    width: 90%;
    margin: auto;
    margin-top: 20px;
    min-height: 500px;
    /* border: 1px solid yellow; */
}

.describing-myself{
    width: 60%;
    margin: 0 auto;
    margin-bottom: 25px;
    padding: 15px 28px;
    display: -webkit-flex;
    display: flex;
    /* border: 1px solid red; */
}

.describing-myself__image {
    width: 160px;
    height: 160px;
    min-width: 160px;
    min-height: 160px;
    margin-right: 40px;
}

.describing-myself__info h1 {
    font-size: 2.44444em;
    margin-bottom: 20px;
    color: var(--gray-dark-color)
}

.info-channel{
    font-size: 18px;
    /* max-width: 70%; */
    line-height: 1.5;
}

.describing-myself__image img {
    width: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.item-news__img {
    width: 90%;
    /* border: 1px solid blue; */
}

.item-news__img img {
    width: 100%;
    border-radius: 20px;
}

.item-news__content {
    padding-top: 20px;
    padding-left: 20px;
}

.item-news__content  h2 {
    font-size: 2em;
    line-height: 30px;
    margin-bottom: 15px;
}
.container-notfound {
    width: 90%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-top: 20px;
}

.image-error {
    width: 30%;
    margin-top: 30px;
    margin-bottom: 40px;
    /* border: 1px solid blue; */
}

.image-error img {
    width: 100%;
}

.btn-back {
    background-color: var(--purple-color);
    padding: 12px 17px;
    border: none;
    border-radius: 4px;
    text-transform: uppercase;
    margin-top: 20px;
    font-weight: bold;
    transition-duration: .5s;
    transition-property: background color;
    transition-timing-function: ease;
    cursor: pointer;
}

.btn-back:hover {
    background-color: var(--purple-dark-color);
    color: #fff;
}
